window.onload = function () {
    $("span").each(function (index, ele) {
        $(this).css({
            color: randColor()
        })
    });
    var canvas = $(".canvas")[0];
    var canvas2 = $(".canvas")[1];
    // canvas=document.getElementById("canvas1");
    var context = canvas.getContext("2d");//2d绘图
    var context2 = canvas2.getContext("2d");
    var W = window.innerHeight;//获取屏幕的宽度
    var H = window.innerWidth;//获取屏幕的高度
    // console.log(W);
    //设置canvans的宽高
    canvas.width = W;
    canvas.height = H;
    canvas2.width = W;
    canvas2.height = H;
    //记录每列文字的y轴坐标
    var drop = [];
    //设置每个文字大小
    var font_size = 14;
    //计算列
    var columns = Math.floor(W / font_size);
    //给每个文字初始化一个起始点的位置
    for (var i = 0; i < columns; i++) {
        drop.push(0);
    }

    var str = "01";
    //绘画函数
    function draw1() {

        context.fillStyle = "rgba(0, 0, 0, 0.05)";//设置画布的背景色和透明度
        context.fillRect(0, 0, W, H); //设置矩形的起始xy坐标，宽和高
        context.font = "1000 " + font_size + "px 微软雅黑";//设置字体样式
        context.fillStyle = randColor();//调用随机颜色函数
        //写入画布
        for (var i = 0; i < columns; i++) {
            var index = Math.floor(Math.random() * str.length);
            var x = i * font_size;
            var y = drop[i] * font_size;
            context.fillText(str[index], x, y);
            //控制刷新的时间
            if (y >= canvas.height && Math.random() > 0.99) {
                drop[i] = 0;
            }
            drop[i]++;
        }
    }
    function draw2() {

        context2.fillStyle = "rgba(0, 0, 0, 0.05)";
        context2.fillRect(0, 0, W, H);
        context2.font = "1000 " + font_size + "px 微软雅黑";
        context2.fillStyle = randColor();

        for (var i = 0; i < columns; i++) {
            var index = Math.floor(Math.random() * str.length);
            var x = i * font_size;
            var y = drop[i] * font_size;
            context2.fillText(str[index], x, y);
            if (y >= canvas2.height && Math.random() > 0.99) {
                drop[i] = 0;
            }
            drop[i]++;
        }
    }
    function randColor() {
        var r = Math.floor(Math.random() * 256);
        var g = Math.floor(Math.random() * 256);
        var b = Math.floor(Math.random() * 256);
        return "rgba(" + r + "," + g + "," + b + ")";

    }

    // setTimeout(draw1,1000);
    // setTimeout(draw2,2000);
    setInterval(draw1, 40);
    setInterval(draw2, 40);


}


