<template>
  <div>
    <div class="text_box">
      <div class="zh">
        <marquee behavior="alternate" direction="" scrollamount="14"><span v-for="item in list1">{{item}}&nbsp;</span>
        </marquee>
      </div>
      <div class="en">
        <marquee behavior="alternate" direction="" scrollamount="4"><span v-for="item in list2">{{item}}&nbsp;</span>
        </marquee>
      </div>
    </div>
    <div>
      <div id="bgcanvas1">
        <canvas class="canvas"></canvas>
      </div>
      <div id="bgcanvas2">
        <canvas class="canvas"></canvas>
      </div>
    </div>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import $ from "jquery";
import "../assets/js/index";
export default {
  // 页面创建前添加body背景色
  beforeCreate() {
    document
      .querySelector("body")
      .setAttribute("style", "background-color:#333;");
  },

  //页面销毁前去掉body背景色
  beforeDestroy() {
    document.querySelector("body").removeAttribute("style");
  },

  data() {
    //这里存放数据
    return {
      list1: [
        "不",
        "要",
        "让",
        "别",
        "人",
        "的",
        "看",
        "法",
        "淹",
        "没",
        "了",
        "自",
        "己",
        "心",
        "底",
        "的",
        "声",
        "音",
        "最",
        "重",
        "要",
        "的",
        ",",
        "要",
        "有",
        "勇",
        "气",
        "跟",
        "随",
        "自",
        "己",
        "的",
        "内",
        "心",
        "与",
        "直",
        "觉"
      ],
      list2: [
        "Don't",
        "let",
        "others'",
        "judgement",
        "submerge",
        "your",
        "inner",
        "voice",
        ".",
        "Most",
        "importantly",
        ",",
        "you",
        "have",
        "to",
        "be",
        "brave",
        "to",
        "follow",
        "your",
        "heart",
        "and",
        "intuition"
      ]
    };
  }
};
</script>
<style scoped>
@import "../assets/css/common.css";
body {
  background-color: #333 !important;
}
#bgcanvas1,
#bgcanvas2 {
  float: left;
  width: 50%;
  height: 100%;
}
.canvas {
  height: 100%;
  width: 100%;
  min-width: 375px;
  min-height: 738px;
}
.text_box {
  position: absolute;
  /*width: 600px;*/
  width: 1920px;
  text-align: center;
  /*color: #fff;*/
  color: rgba(255, 255, 255, 0.4);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: auto;
  font-size: 32px;
  font-family: font-Giorgio, PingFangSCS, Arial, sans-serif;
}
.text_box div {
  /*width: 800px;*/
  margin-top: 15px;
}
.footer {
  position: absolute;
  width: 100%;
  z-index: 99;
  bottom: 20px;
  text-align: center;
}
</style>


